.manage-heading{
    letter-spacing: 2px;
    text-align: center;
    font-size: 1.6rem;
    font-weight: bold;
    margin: 20px;
}

.all-my-posts{
    padding: 30px 20px;;
    border: 1px solid lightgreen;
    margin-bottom: 20px;
}


.my-posts-titles{
    color: green;
    font-weight: bold;
    letter-spacing: 2px;
    margin-bottom: 20px;
}

.posts-vote{
    color: blue;
}

.posts-date-time{
    color: blue;
    letter-spacing: 2px;
    margin-right: 5px;
    font-weight: bold;
    font-size: 0.8rem;
}

.posts-delete{
    float: right;
}

.posts-responses{
    border: 0;
    border: 1px solid green;
    background: none; 
    text-align: center; 
    border-radius: 10px;
    transition: 0.25s;
    font-weight: bold;
    padding: 10px 15px;
    margin-top: 30px;
    display: block;
}

.posts-responses:hover{
    color: white;
    background-color: green;

}

.my-posts-post{
    padding: 20px;
    background-color: lightgray;
    margin-bottom: 10px;
}