.my-home{
    /* position: fixed; */
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    /* background: #E9F8F9; */
    /* z-index: 10; */
    padding-bottom: 300px; 
}

.home-headings{
    margin-top: 30px;
    margin-bottom: 30px;
    text-align: center;
    letter-spacing: 2px;
}

.home-headings h2,h3{
    font-weight: bold;
}


.dictionary{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }


.dictionary > .term {
    border: 0.1px solid lightgray;
    margin: 20px;
    transition: all 100ms ease-in-out;
    
}

.term{
    border-radius: 10px;
    box-shadow: 1px 2px 5px gray;
}



.box-component{
    background-color: #ffffff;
    width: 200px;
    height: 200px;
    border-radius: 10px;
    text-align: center;
    padding-top: 33px;
}

.term:hover, .box-component:hover{
    border-radius: 50%;   
}

.box-component p{
    margin-top: 20px;
    font-weight: bold;
    color: green; 
    /* text-shadow: 1px 1px 2px lightgray; */
    letter-spacing: 2px;
    
}

.f-size{
    font-size: 3rem;
    /* color: #6B728E; */
    /* color: green; */
    color: black;
}