.profile-image{
    width: 100px;
    height: 100px;
    margin: auto;
    border-radius: 100%;
    border: 1px solid lightgray;
    text-align: center;
    padding: 30px;
    font-size: 1.5rem;
    box-shadow: 1px 2px 2px black;
    font-weight: bold;
    background-color: lightgreen;
    margin-bottom: 30px;
    margin-top: 50px;
}

.card{
    /* width: 500px; */
    margin: 100px auto;
    /* border: 2px solid gray; */
    box-shadow: 3px 5px 5px blue, -3px -5px 5px green; 
    background-color: #F3E8FF;
}


.info-rest{
    font-size: 1.3rem;
    letter-spacing: 2px;
    font-weight: bold;
    text-align: center;
}

.center-me{
    border: 0;
    border: 1px solid green;
    background: none;
    display: block;
    margin: 20px auto; 
    text-align: center;
    padding: 15px 30px; 
    /* color: white; */
    border-radius: 20px;
    transition: 0.25s;
    font-weight: bold;
}

.center-me:hover{
    background-color: #2ecc71;
  }


.profile-name{
    letter-spacing: 2px;
    color: green;
    font-size: 0.8rem;
    font-weight: bold;
}  

.profile-surname{
    letter-spacing: 2px;
    color: blue;
    font-size: 0.8rem;
    font-weight: bold;
}  

.profile-email{
    letter-spacing: 2px;
    color: green;
    font-size: 0.8rem;
    font-weight: bold;
}  